<template>
    <div class="workDelayApply" >
        <navigation-bar
          pageName="延期申请（业主）"
          @onLeftClick="goBack"/>
        <div class="box">
            <div class="field-box">
                <div class="approver-box">
                  <van-cell
                    title="审批人" />
                  <div class="dis" v-if="postInfo">
                    <span>您的审批人是 </span>
                    <span>{{postInfo.postName}}</span>
                    <span v-if="postInfo.userList && postInfo.userList.length > 0">(</span>
                    <span v-if="postInfo.userList" v-for="(item, index) in (postInfo.userList.length > 3 ? 3 : postInfo.userList.length)" :key="index">
                      {{postInfo.userList[index].userName}}
                    </span>
                    <span v-if="postInfo.userList && postInfo.userList.length > 3">...</span>
                    <span v-if="postInfo.userList && postInfo.userList.length > 0">)</span>
                  </div>
                </div>

                <van-field
                    readonly
                    clickable
                    label="变更类型"
                    :value="applyType ? applyType.typeName : ''"
                    input-align="right"
                    placeholder="请选择类型"
                    right-icon="arrow"
                    label-width="130"
                    @click="selectType"/>

                <van-field
                    readonly
                    clickable
                    label="延时时间"
                    :value="showTime"
                    input-align="right"
                    placeholder="请选择延时时间"
                    right-icon="arrow"
                    @click="showDatePicker = true"
                    v-if="!applyType || applyType && applyType.type == '0'"/>
                <van-field
                    readonly
                    clickable
                    label="工单分类"
                    :value="category? category.complCatName : ''"
                    input-align="right"
                    placeholder="请选择工单分类"
                    right-icon="arrow"
                    @click="selecCategory"
                    v-if="applyType && applyType.type == '1'"/>
            </div>
            <div class="area-box">
                <textarea name="" id="" placeholder="请填写申请原因..." v-model="workContent" maxlength="100" @input="filterContent"></textarea>
                <div class="num">{{workContent.length}}/100</div>
            </div>
            <div class="submit-box">
                <div class="submit" @click="submitApply">提交</div>
            </div>
        </div>
        <van-popup v-model="showFilterCondition" position="bottom">
          <van-picker
            :columns="conditionList"
            show-toolbar
            title="选择工单分类"
            value-key="typeName"
            :default-index="defalutIndex"
            @cancel="showFilterCondition = false"
            @confirm="confirmSelect"/>
        </van-popup>

        <van-popup v-model="showDatePicker" position="bottom">
          <van-datetime-picker
            v-model="workDate"
            type="datetime"
            title="选择延时时间"
            :min-date="minDate"
            :max-date="maxDate"
            @cancel="showDatePicker = false"
            @confirm="selectDate"
            />
        </van-popup>
        <van-popup v-model="showCategoryModel" position="bottom">
          <div class="popup-top">
            <div class="cancel" @click="hideModel">
              <img :src="leftIcon" alt="">
            </div>
            <div class="catTitle">{{catTitle}}</div>
            <!-- <div class="back" @click="backToLastLevel" v-if="uuidParents.length > 1">
              返回上一级
            </div> -->
          </div>
          <div class="dis-tab">
            <div class="content-div" v-for="(item, index) in tabs" @click="toClass(item)" >
              <div class="dis-tab-div" v-if="classData.level != index">
                <div class="type-tab">{{item.catName}}</div>
              </div>
              <div class="dis-tab-div-sel" v-if="classData.level == index">
                <div class="type-tab" >{{item.catName}}</div>
              </div>
            </div>
          </div>
          <div class="popup-bottom">
            <div v-for="(item, index) in classList" :key="index" @click="nextClass(item)">
              <div class="detail-cell section-select" v-if="item.uuid != selectTab.uuid">
                  <div class="left-nosel" >{{item.complCatName}}</div>
              </div>
              <div class="detail-cell section-select" v-if="item.uuid == selectTab.uuid">
                  <div class="left" >{{item.complCatName}}</div>
                  <div class="right">
                      <van-icon name="success" size="15px" class="succ-right"/>
                  </div>
              </div>
            </div>
          </div>
        </van-popup>
    </div>
</template>

<script>
import { delayApply, getClassList, getApprover } from "../../services/workPlanService";
import { store } from "../../utils/store";
import log from "../../utils/log";
import leftIcon from "@/assets/imgs/workPlan/left_black.png";
import moment from 'moment';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "workDelayApply",
  components: { NavigationBar },
  data() {
    return {
      approver: "",
      workDate: new Date(),
      showTime: "",
      category: null,
      workDetail: null,
      workContent: "",
      showDatePicker: false,
      showFilterCondition: false,
      showCategoryModel: false,
      applyType: null,
      postInfo: null,

      // 获取工单分类目录需要的参数值
      uuidParents: [],

      classData: {
        uuidParent: "0",
        complResource: 0,
        level: 0,
        deptCode: ''
      },
      classList: [],
      defalutIndex: 0,
      conditionList: [],
      applayTypeList: [
        {
          type: "0",
          typeName: "申请延期"
        },
        {
          type: "1",
          typeName: "申请修改工单分类"
        }
      ],
      minDate: new Date(1919, 0, 1),
      maxDate: new Date(2090, 11, 31),
      userInfo: null,
      leftIcon: leftIcon,
      tabs: [{uuid: 0, catName:"请选择"}],
      catTitle: "请选择一级分类",
      selectTab: {uuid: 0, catName:"请选择"},
    };
  },
  created() {
    this.workDetail = this.$route.params;
    this.userInfo = store.get("userInfo");
    this.getApprover();
  },
  updated() {
    if (this.showCategoryModel) {
      if (this.classData.level == this.tabs.length - 1) {
        // scroll to the cat tab end when receive model show
        document.querySelector(".dis-tab").scrollLeft = 999999;
      } else if (this.classData.level == 0) {
        document.querySelector(".dis-tab").scrollLeft = 0;
      } else {
        let disTab = document.querySelector('.dis-tab');
        let tabSelect = document.querySelector('.dis-tab-div-sel');
        let leftOffset = tabSelect.offsetLeft - disTab.scrollLeft;
        let widthOffset = disTab.offsetWidth - tabSelect.offsetLeft;
        log.l("leftOffset value " + leftOffset + " - widthOffset value " + widthOffset + " - selectWidth value " + tabSelect.offsetWidth);
        if (leftOffset < 40) {
          disTab.scrollLeft = disTab.scrollLeft - 2 * tabSelect.offsetWidth;
        } else if (widthOffset < 40) {
          disTab.scrollLeft = disTab.scrollLeft + 2 * tabSelect.offsetWidth;
        }
      }
    }
  },
  methods: {

    /**
     * 页面返回事件
     */
    goBack: function() {
      this.$router.back(-1);
    },

    /**
     * 获取审批人
     */
    getApprover: function() {
      console.log(this.userInfo)
      getApprover({
        areaCode: this.workDetail.areaCode,
        userCode: this.userInfo.USER_CODE,
        complResource: 0
      }).then(res => {
        if(res.resultCode == 0) {
          console.log(res.data)
          this.postInfo = res.data;
        } else {
          this.$toast(res.msg);
        }
      }, error => {
        this.$toast(error)
      })
    },

    /**
     * 选择审批类型
     */
    selectType: function() {
      this.showFilterCondition = true;
      this.conditionList = this.applayTypeList;
    },

    /**
     * 选择日期事件
     */
    selectDate: function(value) {
      this.showDatePicker = false;
      if (this.workDetail &&
          this.workDetail.compLimitTime &&
          value) {
          let showTimeDate = this.formatter(value);
          let limitTimeDate = this.workDetail.compLimitTime;
          if (moment(showTimeDate) < moment(limitTimeDate)) {
              this.$toast("延期时间必须大于规定时限，请重新选择延期时间！");
              return;
          }
      }
      this.showTime = this.formatter(value);
      this.workDate = value;
    },

    /**
     * 格式化日期
     */
    formatter: function(date) {
      return (
        date.getFullYear() +
        "-" +
        this.getTime(date.getMonth() + 1) +
        "-" +
        this.getTime(date.getDate()) +
        " " +
        this.getTime(date.getHours()) +
        ":" +
        this.getTime(date.getMinutes()) + ":00"

      );
    },

    /**
     * 获取规则的格式
     */
    getTime(time) {
      if (time < 10) {
        return "0" + time;
      } else {
        return time;
      }
    },

    /**
     * 确认选择
     */
    confirmSelect: function(item, index) {
      this.showFilterCondition = false;
      if (!this.applyType || item.type != this.applyType.type) {
        this.applyType = item;
        this.defaultIndex = index;
        this.showTime = "";
        this.category = "";
      }
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.workContent = this.workContent.replace(
        /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/gi,
        ""
      );
    },

    /**
     * 返回上一级
     */
    backToLastLevel: function() {
      this.uuidParents.pop();
      this.classData.uuidParent = this.uuidParents[
        this.uuidParents.length - 1
      ];
      this.tabs = [{uuid: 0, catName:"请选择"}];
      this.classData.level = 0;
      this.catTitle = "请选择一级分类";
      this.selecCategory();
    },

    /**
     * 点击列表进入下一级
     */
    nextClass: function(item) {
      if (item.isLeafFlag == 0) {
        // 不是最后一级
        this.classData.uuidParent = item.uuid;
        if (this.classData.level < this.tabs.length - 1) {
          // update tabs
          let levelIndex = this.classData.level;
          this.tabs = this.tabs.slice(0, levelIndex);
        } else {
          this.tabs.pop();
        }
        this.tabs.push({uuid:item.uuid, catName:this.getCatName(item.complCatName)});
        this.selectTab = {uuid: 0, catName:"请选择"};
        this.tabs.push(this.selectTab);
        this.classData.level++;
        this.selecCategory();
      } else {
        this.category = item;
        this.showCategoryModel = false;
        this.tabs = [{uuid: 0, catName:"请选择"}];
        this.selectTab = {uuid: 0, catName:"请选择"};
        this.classList = [];
        this.classData = {
          uuidParent: "0",
          complResource: 0,
          level: 0,
          deptCode: this.userInfo.deptCode
        };
      }
    },

    /**
    * 进入指定分类级别
    **/
    toClass: function(item) {
      if (item.uuid == this.selectTab.uuid) {
        log.i("WorkDelayApply: select the same tab, ignore.");
        return;
      }
      // update class list
      let levelIndex = this.tabs.indexOf(item);
      let lastItem = this.tabs[levelIndex - 1];
      this.selectTab = item;
      if (lastItem) {
        this.classData.uuidParent = lastItem.uuid;
        this.classData.level = levelIndex;
        this.selecCategory();
        // update tab list
        /*this.tabs = this.tabs.slice(0, levelIndex);
        this.tabs.push({uuid: 0, catName:"请选择"});*/
      } else {
        this.classData = {
          uuidParent: "0",
          complResource: 0,
          level: 0,
          deptCode: this.userInfo.deptCode,
        };
        // this.tabs = [{uuid: 0, catName:"请选择"}];
        this.selecCategory();
      }
    },

    getCatName: function(complCatName) {
      if (!complCatName) {
        return null;
      }
      if (complCatName.length <= 5) {
        return complCatName;
      } else {
        return complCatName.slice(0, 2) + "..." + complCatName.slice(complCatName.length - 2);
      }
    },

    /**
     * 选择工单分类点击事件
     */
    selecCategory: function() {
      if (
        this.classData.uuidParent !=
        this.uuidParents[this.uuidParents.length - 1]
      ) {
        this.uuidParents.push(this.classData.uuidParent);
      }
      this.classData.complResource = 0;
      this.classData.deptCode = this.userInfo.deptCode;
      getClassList(this.classData).then(
        res => {
          this.classList = res.data;
          this.showCategoryModel = true;
          switch (this.classData.level + 1) {
            case 1:
              this.catTitle = "选择一级分类";
              break;
            case 2:
              this.catTitle = "选择二级分类";
              break;
            case 3:
              this.catTitle = "选择三级分类";
              break;
            case 4:
              this.catTitle = "选择四级分类";
              break;
            case 5:
              this.catTitle = "选择五级分类";
              break;
            case 6:
              this.catTitle = "选择六级分类";
              break;
            case 7:
              this.catTitle = "选择七级分类";
              break;
            case 8:
              this.catTitle = "选择八级分类";
              break;
            case 9:
              this.catTitle = "选择九级分类";
              break;
            case 10:
              this.catTitle = "选择十级分类";
              break;
            default:
              // not care
              this.catTitle = "选择超十级分类";
              break
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 取消弹窗
     */
    hideModel: function() {
      this.classData.uuidParent = "0";
      this.uuidParents = [];
      this.showCategoryModel = false;
      this.tabs = [{uuid: 0, catName:"请选择"}];
      this.classData.level = 0;
      this.catTitle = "请选择一级分类";
      this.classList = [];
      this.selectTab = {uuid: 0, catName:"请选择"};
    },

    /**
     * 提交延期申请
     */
    submitApply: function() {
      if (!this.hasCompleteParam()) {
        return;
      }

      var delayData = {
        approvalType: this.applyType.type * 1,
        approvalPostCode: this.postInfo.postCode,
        complCatCodeOld: this.workDetail.complCatCode,
        complCatCodeNew: this.category ? this.category.complCatCode : '',
        receveDelayTime: this.showTime || '',
        reason: this.workContent,
        complCode: this.workDetail.complCode,
        userCode: this.userInfo.USER_CODE,
        areaCode: this.workDetail.areaCode,
        areaName: this.workDetail.areaName,
      };

      delayApply(delayData).then(
        res => {
          if (res.resultCode == 0) {
            this.$toast("提交工单申请成功");
            store.set("workDetailComplCode", this.workDetail.complCode);
            this.goBack();
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      if (!this.workDetail || !this.workDetail.compLimitTime) {
        this.$toast("未接单,请接单后重试!");
        return false;
      }
      if(!this.postInfo) {
        this.$toast("没有对应的审核人");
        return false;
      }
      if (!this.applyType) {
        this.$toast("请选择延期类型！");
        return false;
      }
      if (this.applyType.type == 0 && !this.showTime) {
        this.$toast("请选择延期时间！");
        return false;
      }
      if (this.applyType.type == 1 && !this.category) {
        this.$toast("请选择工单分类！");
        return false;
      }
      if (!this.workContent) {
        this.$toast("申请原因不能为空！");
        return false;
      }
      if (
        this.applyType == 0 &&
        this.workDetail &&
        this.workDetail.compLimitTime &&
        this.showTime
      ) {
          if (moment(this.showTime) < moment(this.workDetail.compLimitTime)) {
              this.$toast("延期时间必须大于规定时限！");
              return false;
          }
      }
      return true;
    }
  },
  computed: {}
};
</script>
<style scoped src="../../assets/css/field.css">
</style>
